import React, {useContext} from "react";
import PropTypes from "prop-types";
import "../app/DefaultCSS.css";
import {GameDiv} from "../shared/game-basic-components/GameDiv";
import {GameRouterContext} from "../shared/game-advance-components/GameRouter";
import {SizeContext} from "../../context/SizeContext";
import {COLOR} from "../shared/styled/ColorsPalette";
import {PARTY} from "../../werewolf-consts/enums";
import {GameText} from "../shared/game-basic-components/GameText";

function RoomItem(props) {
    const {id} = props;
    const router = useContext(GameRouterContext);
    const {basic} = useContext(SizeContext);

    function gotoRoom(id: any) {
        if (router.routeTo) router.routeTo("gameRoom", [id]);
    }

    return (
        <GameDiv
            className="GameBtn"
            $backgroundColor={COLOR[PARTY.BETRAYER]}
            m={"1rem"}
            round={"1rem"}
            onClick={() => gotoRoom(id)}
            w={basic.mobileView ? basic.gameW * 0.9 : basic.gameW / 2.3}
        >
            <GameText>{id}</GameText>
        </GameDiv>
    );
}

RoomItem.propTypes = {
    id: PropTypes.string
};

export default RoomItem;
