import React, {useEffect, useState} from "react";

import {importScript} from "../../utils/react_utils";

import {LoginForm} from "./LoginForm";
import "./App.css";
import HomeScreen from "../home-screen/HomeScreen";
import {LobbyRoomScreen} from "../lobby-screen/LobbyRoomScreen";
import GameRoomRouter from "../game-screens/GameRoomRouter";
import {FacebookPlayer, FacebookUserContext} from "../../context/facebook-context";
import TestScreen from "../game-screens/TestScreen";
import Grid from "../shared/styled/game-layout/Grid";
import useWindowSize from "../../utils/custom-hooks/useWindowSize";
import {COLOR} from "../shared/styled/ColorsPalette";
import backGround_m from "../../assets/images/background_home-m.jpg";
import backGround_l from "../../assets/images/background_home-l.jpg";
import GameRouter from "../shared/game-advance-components/GameRouter";
import {db, FirestoreContext} from "../../context/firestore-context";
import {SizeContext} from "../../context/SizeContext";
import useBasicSize from "../../utils/custom-hooks/useBasicSize";
import {GameImage} from "../shared/game-basic-components/GameImage";
import GameLayout from "../shared/game-advance-components/GameLayout";
import {useFirestoreDocument} from "../../library/flashstore/utils/hooks";
import {User} from "../../database/firestore/User";

const DEFAULT_AVT =
    "https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcSYGWvwGxXt1ghLMWvsXmI82WdSAtgPntBkhg&usqp=CAU";

function App() {
    const [player, setPlayer] = useState({} as FacebookPlayer);
    const S = useWindowSize();
    const BS = useBasicSize(S);
    useEffect(() => {
        // load fbInstant script
        const script = importScript("https://connect.facebook.net/en_US/fbinstant.6.2.js");
        script.onload = async () => {
            try {
                await window.FBInstant.initializeAsync();
                await window.FBInstant.startGameAsync();
                let result = await window.FBInstant.player.getSignedPlayerInfoAsync("my_metadata");
                const user = {
                    playerId: window.FBInstant.player.getID() || undefined,
                    playerName: "FB-" + window.FBInstant.player.getName(),
                    playerPic: window.FBInstant.player.getPhoto() || DEFAULT_AVT,
                    signature: result.getSignature()
                };
                onLogin(user);
            } catch (e) {}
        };
        document.body.appendChild(script);
        return () => {
            document.body.removeChild(script);
        };
    }, []);
    const onLogin = async ({playerId, playerName, playerPic, signature}: Partial<FacebookPlayer>) => {
        const uid = playerId || "fakeID";
        let player: FacebookPlayer = {
            playerId: uid,
            playerName: playerName || uid,
            playerPic: playerPic || DEFAULT_AVT,
            signature: signature || "fake"
        };
        setPlayer(player);
        if (player.signature !== "fake") {
            console.log("save DB");
            const userDoc = db.users.document(player.playerId);
            const userData = await userDoc.get();
            if (userData == null) {
                console.log("write DB");
                await userDoc.set(new User(player.playerName, player.playerPic));
            }
        }
    };
    return (
        <SizeContext.Provider value={{window: S, basic: BS}}>
            <GameImage
                position={"absolute"}
                w={S.width}
                h={S.height}
                src={S.isVertical ? backGround_m : backGround_l}
            />
            <GameLayout.Col position={"relative"} h={S.height} w={S.width} scroll={false}>
                <Grid.FullHeightRow>
                    <FirestoreContext.Provider value={db}>
                        <FacebookUserContext.Provider value={player}>
                            {!player.playerId ? (
                                <header className="App-header">
                                    <LoginForm onLogin={onLogin} />
                                </header>
                            ) : (
                                <GameRouter paths={["home", "test", "lobby", "gameRoom"]}>
                                    <HomeScreen />
                                    <TestScreen />
                                    <LobbyRoomScreen />
                                    <GameRoomRouter />
                                </GameRouter>
                            )}
                        </FacebookUserContext.Provider>
                    </FirestoreContext.Provider>
                </Grid.FullHeightRow>
            </GameLayout.Col>
        </SizeContext.Provider>
    );
}

export default App;
