import {styled} from "styletron-react";
import {getBasicCSS, IBasicProps} from "../game-basic-components/GameDiv";
import {COLOR} from "./ColorsPalette";

const InputTextView = styled<any, IBasicProps>("input", (props) => {
    let customProps = {...props};
    customProps.round = "1rem";
    customProps.$backgroundColor = COLOR.inputBackground;
    customProps.height = 32;
    customProps.px = ".3rem";
    return {
        ...getBasicCSS(customProps),
        textAlign: "left",
        wordBreak: "break-all",
        border: "none",
        flex: 1,
        ":focus": {
            outline: "none"
        }
    };
});

export default InputTextView;
