import React, {useContext, useState} from "react";
import {FacebookPlayer} from "../../context/facebook-context";
import GameLayout from "../shared/game-advance-components/GameLayout";
import {GameText} from "../shared/game-basic-components/GameText";
import {SizeContext} from "../../context/SizeContext";
import GoogleIcon from "../../assets/game-btn/google.svg";
import GameIconButton from "../shared/game-basic-components/GameIconButton";
import firebase from "firebase";
import {googleAuthProvider} from "../../context/firestore-context";
import {POSITION} from "../shared/styled/game-layout/StackLayout";
import {COLOR} from "../shared/styled/ColorsPalette";
import {GameDiv} from "../shared/game-basic-components/GameDiv";

interface LoginFormProps {
    onLogin: ({playerId, playerName, playerPic, signature}: Partial<FacebookPlayer>) => any;
}

export function LoginForm(props: LoginFormProps) {
    const {basic, window} = useContext(SizeContext);
    const [playerId, setPlayerId] = useState("1");

    function quickLogin(key) {
        //
        if (key.keyCode >= 49 && key.keyCode <= 57) props.onLogin({playerId: key.keyCode + ""});
    }

    function googleLogin() {
        firebase
            .auth()
            .signInWithPopup(googleAuthProvider)
            .then(function (result) {
                // This gives you a Google Access Token. You can use it to access the Google API.
                // var token = result.credential?.accessToken;
                // The signed-in user info.
                const user = result.user;
                const player = {
                    playerId: "gg-" + user?.uid,
                    playerName: user?.displayName || undefined,
                    playerPic: user?.photoURL || undefined,
                    signature: "GG"
                };
                console.log(player);
                props.onLogin(player);
                // ...
            })
            .catch(function (error) {
                // Handle Errors here.
                const errorCode = error.code;
                const errorMessage = error.message;
                // The email of the user's account used.
                const email = error.email;
                // The firebase.auth.AuthCredential type that was used.
                const credential = error.credential;
                // ...
            });
    }

    return (
        <>
            <GameLayout.Col $justifyContent={"flex-start"} $alignItems={"center"} w={window.width} h={window.height}>
                <GameLayout.Row
                    m={".5rem"}
                    $alignItems={"center"}
                    $alignSelf={"flex-start"}
                    w={window.isVertical ? window.width * 0.8 : window.width / 3}
                >
                    <GameText m={".5rem"} size={basic.size9} $fontStyle={"Altus"}>
                        @masoibot
                    </GameText>
                </GameLayout.Row>
                <GameLayout.ColExpanded
                    $alignSelf={window.isVertical ? "center" : "flex-start"}
                    $alignItems={"center"}
                    $justifyContent={"center"}
                    w={window.isVertical ? window.width * 0.8 : window.width / 3}
                    pl={window.isVertical ? 0 : "3rem"}
                >
                    <GameText size={basic.size10 * 2} mt={"1rem"} $fontStyle={"Butcherandblock"} $fontWeight={"border"}>
                        MA SÓI
                    </GameText>
                    <GameDiv onKeyUp={quickLogin}>
                        <GameIconButton
                            $backgroundColor={COLOR.blurDark50}
                            round={"3rem"}
                            src={GoogleIcon}
                            size={basic.iconButtonSize}
                            onClick={googleLogin}
                            py={".5rem"}
                            px={"1rem"}
                            textPosition={POSITION.RIGHT}
                        >
                            LOGIN WITH GOOGLE
                        </GameIconButton>
                        <br />
                        <input
                            name={"playerId"}
                            placeholder={"playerId"}
                            onChange={(event) => setPlayerId(event.target.value)}
                        />
                        <button onClick={() => props.onLogin({playerId})}>Login</button>
                    </GameDiv>
                </GameLayout.ColExpanded>
            </GameLayout.Col>
        </>
    );
}
