import {DocumentData, FirestoreDocument, LinkFirestoreDocumentArray} from "../../library/flashstore";
import {Village} from "./Village";
import {Collections} from "./CollectionNames";

export class User extends DocumentData {
    @LinkFirestoreDocumentArray(Collections.VILLAGES)
    villages: FirestoreDocument<Village>[];

    @LinkFirestoreDocumentArray(Collections.USERS)
    following: FirestoreDocument<User>[];

    @LinkFirestoreDocumentArray(Collections.USERS)
    followers: FirestoreDocument<User>[];

    constructor(public name: string, public avatarUrl: string) {
        super();
        this.villages = [];
        this.following = [];
        this.followers = [];
    }
}
